import React from "react"
import PropTypes from "prop-types"
import styled from "react-emotion"
import { Link } from "gatsby"

export const SkeletonStyledInternalLink = styled(Link)``
export const SkeletonStyledExternallLink = styled(`a`)``

export const linkPropTypes = {
  to: PropTypes.string,
  href: PropTypes.string,
}

const linkSkeletonPropTypes = {
  ...linkPropTypes,
  StyledComponent: PropTypes.any,
}

export const linkDefaultPropTypes = {}

const linkSkeletonDefaultPropTypes = {
  ...linkDefaultPropTypes,
  StyledComponent: SkeletonStyledInternalLink
}

const LinkSkeleton = ({
  StyledComponent,
  children,
  to,
  href,
  ...rest
}) => {
  if (href) {
    return (
      <StyledComponent href={href} {...rest}>
        {children}
      </StyledComponent>
    )
  }

  return (
    <StyledComponent to={to} {...rest}>
      {children}
    </StyledComponent>
  )
}

LinkSkeleton.propTypes = linkSkeletonPropTypes
LinkSkeleton.defaultProps = linkSkeletonDefaultPropTypes

export default LinkSkeleton
